#board {
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-areas: "tasks-todo tasks-wip tasks-done";
  gap: 1rem;
  padding: 1rem;
  display: grid;
}

#board > section {
  box-shadow: 3px 3px 9px #00000040;
}

#board > section > h2 {
  text-align: center;
  text-transform: uppercase;
  color: #00000080;
  margin: 0 0 1rem;
  font-size: 3rem;
  font-weight: 900;
}

#board > section > form, #board > section > div {
  flex-direction: column;
  gap: .4rem;
  padding: .5rem;
  display: flex;
}

#board article {
  position: relative;
}

#tasks-todo {
  grid-area: tasks-todo;
}

#tasks-wip {
  grid-area: tasks-wip;
}

#tasks-done {
  grid-area: tasks-done;
}

.task-box {
  padding: 1.2rem;
}

#tasks-todo .task-box {
  padding-bottom: 1rem;
}

#tasks-wip .task-box {
  padding-bottom: 1.5rem;
}

#tasks-done .task-box {
  padding-bottom: 2rem;
}

.assigned-to {
  font-style: italic;
}

.assigned-to:before {
  content: " ► ";
}

.task-assign-form {
  grid-template-columns: 3fr 1fr;
  justify-content: center;
  align-items: stretch;
  gap: 0;
  margin: 1.5rem 0 0;
  padding: 1rem 1rem 0;
  display: grid;
}

.task-assign-form input, .task-assign-form button {
  border: 0;
  outline: 0;
  margin: 0;
  padding: .5rem;
}

.task-assign-form input {
  background-color: #e7e7e7;
  border-radius: 15px 0 0 15px;
  padding-left: 1rem;
}

.task-assign-form button {
  color: #fff;
  cursor: pointer;
  background-color: #000;
  border-radius: 0 15px 15px 0;
}

.task-assign-form button:hover, .task-assign-form button:focus, .task-assign-form button:active {
  color: #000;
  background-color: orange;
}

.task-button-wrapper {
  justify-content: flex-end;
  margin: 1.5rem 0 0;
  display: flex;
}

#tasks-wip-box button {
  color: #fff;
  cursor: pointer;
  background-color: #000;
  border: 0;
  border-radius: 15px 0 0;
  outline: 0;
  padding: .5rem 1rem;
  position: absolute;
  bottom: 0;
  right: 0;
}

#tasks-wip-box button:hover, #tasks-wip-box button:focus, #tasks-wip-box button:active {
  color: #000;
  background-color: orange;
}

#tasks-done-box button {
  color: #fff;
  cursor: pointer;
  background-color: #000;
  border: 0;
  border-radius: 15px 0 0;
  outline: 0;
  width: 30px;
  height: 30px;
  padding: .5rem;
  position: absolute;
  bottom: 0;
  right: 0;
}

#tasks-done-box button:hover, #tasks-done-box button:focus, #tasks-done-box button:active {
  background-color: #8b0000;
}

